import React, {useEffect, useState} from 'react'
import {useSelector, useDispatch} from 'react-redux'
import './Form.css'

export default function Form() {

   const [article, setArticle] = useState({
      title:"",
      body:""
   });

   const dispatch = useDispatch();
   
   const addNewTitle = e => {
      const newObjState = {...article, title: e.target.value}
      setArticle(newObjState)
   }
   
   const addNewBody = e => {
      const newObjState = {...article, body: e.target.value}
      setArticle(newObjState)
   }

   const handleForm = e => {
      e.preventDefault();

      const newArticle = {
         title: article.title,
         body: article.body
      }

      dispatch({
         type: "ADDARTICLES",
         payload: article
      })

      setArticle({
         title:"",
         body:""
      })
   }
   
   return (
      <>
         <h1 className="title-form">Écrivez un article</h1>
         <form onSubmit={handleForm} action="" className="container-form">
            <label htmlFor="title">Titre</label>
            <input value={article.title} onInput={addNewTitle} type="text" id="title" placeholder="Entrez le titre" />
            <label htmlFor="article">Votre article</label>
            <textarea value={article.body} onInput={addNewBody} id="article" placeholder="Votre article">Votre article</textarea>
            <button>Envoyez l'article</button>
         </form>
      </>
   )
}
